  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 25px;
  font-family:'Montserrat';
}
nav a:hover{
  color:white;
}

nav .animation{
  position: absolute;
  height: 5px;
  bottom: 16px;
  z-index: 0;
  background: gold;
  border-radius: 8px;
  transition: all .7s ease 0s;
}

a:nth-child(1){
  width: 130px;
}
nav .start-home,a:nth-child(1):hover~.animation{
  width: 85px;
  left: 10px;
}
a:nth-child(2){
