  width: 107px;
  left: 296px;
}
a:nth-child(4){
  width: 130px;
}
nav .start-aboutus,a:nth-child(4):hover~.animation{
  width: 100px;
  left: 406px;
}
a:nth-child(5){
  width: 130px;
}
nav .start-collaborate,a:nth-child(5):hover~.animation{
  width:110px;
  left: 520px;
}


@-webkit-keyframes fadeInAnimation {
  from { 
    opacity: 0;
    height: 10px;
  }
  to   { 
    opacity: 1; 
    height: 210px;
  }
}


@keyframes fadeInAnimation {
  from { 
    opacity: 0;
    height: 10px;
  }
  to   { 
    opacity: 1; 
    height: 210px;
  }
}

@-webkit-keyframes fadeOutAnimation {
  from { 
    opacity: 1; 
    height: 210px;
  }
  to   { 
    opacity: 0;
    height: 10px;
  }
}

@keyframes fadeOutAnimation {
  from { 
    opacity: 1; 
    height: 210px;
  }
  to   { 
    opacity: 0;
    height: 10px;
  }
}
@-webkit-keyframes fadeInAnimation32 {
  from { 
    opacity: 0;
    height: 0;
    top: 40%;
    width: 0;
    left: 50%;
  }
  to   { 
    opacity: 1; 
    height: 40%;
    top: 20%;
    width: 80%;
    left: 10%;
  }
}
@keyframes fadeInAnimation32 {
  from { 
    opacity: 0;
    height: 0;
    top: 40%;
    width: 0;
    left: 50%;
  }
  to   { 
    opacity: 1; 
    height: 40%;
    top: 20%;
    width: 80%;
    left: 10%;
  }
}

.animating_popup{
  position: absolute;
  left: 10%;
  top: 20%;
  width: 80%;
  height: 40%;
  -webkit-animation: fadeInAnimation32 1s;
          animation: fadeInAnimation32 1s;
}


