}
.login-page .form .login {
  margin-top: -31px;
  margin-bottom: 20px;
  
}
.loginsignup-page .form .login {
  margin-top: -31px;
  margin-bottom: 20px;
  
}


.form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 450px;
  margin: 0 auto 82px;
  padding: 40px;
  text-align: center;
  border-radius: 20px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 1rem;
  border-radius: 20px;
  text-align: center;
}
.form input:focus{
     border: 2px solid rgba(0, 0, 0, 0.18) !important;

}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background-color: #328f8a;
  background-image: linear-gradient(45deg, #b95ce4, #4f29cd);
  width: 70%;
  border: 0;
  padding: 10px;
  color: #ffffff;
  font-size: 14px;
  transition: all 0.3 ease;
  cursor: pointer;
  border-radius: 50px;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #a116ef;
  text-decoration: none;
}


